<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped src="./LoginPage.scss">
</style>
<template src="./LoginPage.html">
  <div></div>
</template>

<script>
import Navbar from '../../components/navbar/Navbar';
import LoadingIndicator from '../../components/loading-indicator/LoadingIndicator';
import LoginService from '../../services/LoginService';
import Config from '../../config/Config';

export default {

  name: 'login-page',
  components: {
    Navbar,
    LoadingIndicator,
  },
  data() {
    return {
      config: Config,
      isLoading: false,
      passcode1: '',
      passcode2: '',
      passcode3: '',
      passcode4: '',
      passcode5: '',
      msg: this.$route.params.aliasid,
    };
  },
  methods: {
    collectInput(message) {
      console.log(message);
    },
    moveToNext(message) {
      document.getElementById(message).focus();
    },
    doFocus() {
      this.click();
    },
  },
  mounted() {
    this.isLoading = true;
    LoginService.getToken(this.$route.params.aliasid).then((response) => {
      console.log(response);
    });
  },
};
</script>



